import React from 'react'

const LoginForm = ({
  handleSubmit,
  handleUsernameChange,
  handlePasswordChange,
  username,
  password
}) => {
  return (
    <div>
      <form onSubmit={handleSubmit} className="commentForm">
        <div>
          Username:
          <br />
          <input id='username'
            value={username}
            onChange={handleUsernameChange}
            className="loginField"
          />
        </div>
        <div>
          Password:
          <br />
          <input id='password'
            type="password"
            value={password}
            onChange={handlePasswordChange}
            className="loginField"
          />
        </div>
        <button id='login-button' type="submit" className="submitButton">login</button>
      </form>
    </div>
  )
}

export default LoginForm
